import { createFeatureSelector, createSelector } from '@ngrx/store';

import { PresentationAmortizationPayment } from '@backbase/data-ang/loans';

import { paymentScheduleStoreKey } from './reducer';
import { PaymentScheduleState } from './store.model';
import { CallState } from '../../models/call-state.model';

const createPaymentScheduleSelector = <T>(projector: (state: PaymentScheduleState) => T) =>
  // eslint-disable-next-line @typescript-eslint/ban-types
  createSelector<object, PaymentScheduleState, T>(createFeatureSelector(paymentScheduleStoreKey), projector);

export const selectAmortizedPayments = createPaymentScheduleSelector(
  ({ amortizedPayments }: PaymentScheduleState): PresentationAmortizationPayment[] => amortizedPayments,
);

export const selectAmortizedPaymentsCallState = createPaymentScheduleSelector(
  ({ amortizedPaymentsCallState }: PaymentScheduleState): CallState => amortizedPaymentsCallState,
);

export const selectAmortizedPaymentsTotalCount = createPaymentScheduleSelector(
  ({ amortizedPaymentsTotalCount }: PaymentScheduleState): number => amortizedPaymentsTotalCount,
);

export const selectAmortizedPaymentsCurrentPage = createPaymentScheduleSelector(
  ({ amortizedPaymentsCurrenPage }: PaymentScheduleState): number => amortizedPaymentsCurrenPage,
);

export const selectAmortizedPayment = createPaymentScheduleSelector(
  ({ amortizedPayment }: PaymentScheduleState): PresentationAmortizationPayment | undefined => amortizedPayment,
);

export const selectAmortizedPaymentCallState = createPaymentScheduleSelector(
  ({ amortizedPaymentCallState }: PaymentScheduleState): CallState => amortizedPaymentCallState,
);
